body {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  font-family: "Montserrat";
  font-size: 14px;
  color: #60606E;
}

.Header {
  position: fixed;
  width: inherit;
  max-width: inherit;
  display: flex;
  height: 60px;
  background-color: #DCDCDC;
}
@media (max-width: 1299px) {
  .Header {
    width: 100%;
  }
}
.Header .Logo {
  display: flex;
  margin: 40px 80px 0px;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 18px;
}
@media (max-width: 768px) {
  .Header .Logo {
    margin: 20px;
  }
}
.Header .BurgerNav {
  position: relative;
  width: inherit;
}
.Header .BurgerNav_HiddenMenu {
  position: fixed;
  z-index: 2;
  height: 60px;
  width: 1080px;
  display: flex;
  padding: 10px;
  top: -100%;
  list-style: none;
  background-color: #CDCDCD;
  transition: top 0.2s;
  font-size: 20px;
  font-weight: bold;
  transform: translateZ(0);
  backface-visibility: hidden;
}
@media (max-width: 1299px) {
  .Header .BurgerNav_HiddenMenu {
    width: 219px;
    display: block;
    right: 0;
    padding-left: 72px;
    height: 100%;
  }
}
.Header .BurgerNav_HiddenMenu_Item {
  margin: 5px 10px;
}
.Header .BurgerNav_HiddenMenu_Item_Link {
  color: #FFFFFF;
}
.Header .BurgerNav_HiddenMenu_Item_Link:hover {
  background-color: #7BEEC7;
  color: #60606E;
}
.Header .BurgerNav_HiddenMenu_Item_Link-active, .Header .BurgerNav_HiddenMenu_Item_Link:active {
  background-color: #FFFFFF;
  color: #60606E;
  transition: linear 0.2s;
}
.Header .BurgerNav_Toogle {
  display: none;
}
.Header .BurgerNav_ButtonMenu {
  position: absolute;
  top: 30px;
  right: 142px;
  padding: 5px;
  display: block;
  transition: top 0.23s;
  z-index: 3;
  cursor: pointer;
  transform: translateZ(0);
  backface-visibility: hidden;
  transition: top 0.2s;
}
.Header .BurgerNav_ButtonMenu span {
  position: relative;
  margin: 5px 0 0;
  display: block;
  height: 2px;
  background-color: #60606E;
  transition: all 0.1s linear 0.23s;
}
.Header .BurgerNav_ButtonMenu span.first {
  width: 30px;
}
.Header .BurgerNav_ButtonMenu span.second {
  width: 15px;
}
.Header .BurgerNav_ButtonMenu span.third {
  width: 25px;
}
.Header .BurgerNav_Toogle:checked ~ .BurgerNav_ButtonMenu {
  top: 0px;
}
.Header .BurgerNav_Toogle:checked ~ .BurgerNav_HiddenMenu {
  top: 0;
}
.Header .BurgerNav_Toogle:checked ~ .BurgerNav_ButtonMenu span.first {
  top: 15px;
}
.Header .BurgerNav_Toogle:checked ~ .BurgerNav_ButtonMenu span.second {
  opacity: 0;
  width: 30px;
}
.Header .BurgerNav_Toogle:checked ~ .BurgerNav_ButtonMenu span.third {
  transform: rotate(-90deg);
  width: 30px;
}

